const { spawn } = require('child_process');

// Define paths to client apps
const clientApps = [
    './client/shell-app',
    './client/user-app',
    './client/checklist-client',
    './client/motivationalTips-client',
    './client/patientPortalApp-client',
    './client/vitalSigns-client',
];

// Start each client app
clientApps.forEach((appPath) => {
    const process = spawn('npm', ['start'], { cwd: appPath, stdio: 'inherit' });

    process.on('exit', (code) => {
        console.log(`[${appPath}]: Process exited with code ${code}`);
    });
});